import React, { useEffect, useRef, useState } from 'react';
import 'ol/ol.css';
import Map from 'ol/Map';
import View from 'ol/View';
import TileLayer from 'ol/layer/Tile';
import VectorLayer from 'ol/layer/Vector';
import VectorSource from 'ol/source/Vector';
import GeoJSON from 'ol/format/GeoJSON';
import OSM from 'ol/source/OSM';
import { fromLonLat } from 'ol/proj';
import { Style, Stroke, Fill, Circle as CircleStyle } from 'ol/style';
import Overlay from 'ol/Overlay';

const magasinStyle = new Style({
  fill: new Fill({
    color: 'rgba(0, 0, 255, 0.6)' // Blue, semi-transparent
  }),
  stroke: new Stroke({
    color: 'blue',
    width: 1
  })
});

const vindkraftOmrStyle = new Style({
  fill: new Fill({
    color: 'rgba(255, 165, 0, 0.6)' // Orange, semi-transparent
  }),
  stroke: new Stroke({
    color: 'darkorange',
    width: 1
  })
});

const MapComponent = () => {
  const mapRef = useRef(null);
  const popupRef = useRef(null);
  const [popupContent, setPopupContent] = useState('');
  const [layerVisibility, setLayerVisibility] = useState({
    points: true,
    lines: true,
    polygons: true
  });

  useEffect(() => {
    const map = new Map({
      target: mapRef.current,
      layers: [
        new TileLayer({ source: new OSM() }),
        layerVisibility.points && new VectorLayer({
          source: new VectorSource({
            url: process.env.PUBLIC_URL + '/Vannkraft_Vannkraftverk.geojson',
            format: new GeoJSON(),
          }),
          style: new Style({
            image: new CircleStyle({  
              radius: 5,
              fill: new Fill({ color: 'rgba(255, 0, 0, 0.8)' }),
              stroke: new Stroke({ color: 'white', width: 1 })
            })
          }),
        }),
        layerVisibility.points && new VectorLayer({
          source: new VectorSource({
            url: process.env.PUBLIC_URL + '/Vindkraft_Vindkraftanlegg.geojson',
            format: new GeoJSON(),
          }),
          style: new Style({
            image: new CircleStyle({  
              radius: 5,
              fill: new Fill({ color: 'rgba(0, 255, 0, 0.8)' }),
              stroke: new Stroke({ color: 'black', width: 1 })
            })
          }),
        }),
        layerVisibility.lines && new VectorLayer({
          source: new VectorSource({
            url: process.env.PUBLIC_URL + '/Elv_Hovedelv.json',
            format: new GeoJSON(),
          }),
          style: new Style({
            stroke: new Stroke({ color: 'blue', width: 2 })
          }),
        }),
        layerVisibility.polygons && new VectorLayer({
          source: new VectorSource({
            url: process.env.PUBLIC_URL + '/filtered_Innsjo_Innsjo.json',
            format: new GeoJSON(),
          }),
          style: magasinStyle
        }),
        layerVisibility.polygons && new VectorLayer({
          source: new VectorSource({
            url: process.env.PUBLIC_URL + '/Vindkraft_VindkraftanleggOmr.geojson',
            format: new GeoJSON(),
          }),
          style: vindkraftOmrStyle
        }),
      ].filter(Boolean),
      view: new View({
        center: fromLonLat([10.7522, 59.9139]),
        zoom: 10,
      }),
    });

    const popup = new Overlay({
      element: popupRef.current,
      positioning: 'bottom-center',
      stopEvent: true,
      offset: [0, -10],
    });
    map.addOverlay(popup);

    map.on('singleclick', function (event) {
      map.forEachFeatureAtPixel(event.pixel, function (feature) {
        const geometry = feature.getGeometry();
        const type = geometry.getType();
        let coordinates;

        switch (type) {
          case 'Point':
            coordinates = geometry.getCoordinates();
            break;
          case 'LineString':
            coordinates = geometry.getCoordinates()[0];
            break;
          case 'Polygon':
            coordinates = geometry.getInteriorPoint().getCoordinates();
            break;
          default:
            return;
        }

        popup.setPosition(coordinates);
        
        const properties = feature.getProperties();
        const name = properties.elvenavn || properties.vannkraftverkNavn || properties.saksTittel || properties.navn || 'No name available';
        setPopupContent(name);
      });
    });

    return () => map.setTarget(undefined);
  }, [layerVisibility]);

  const handleLayerToggle = (layerType) => {
    setLayerVisibility(prevState => ({
      ...prevState,
      [layerType]: !prevState[layerType]
    }));
  };

  return (
    <div>
      <div>
        <label>
          Points
          <input
            type="checkbox"
            checked={layerVisibility.points}
            onChange={() => handleLayerToggle('points')}
          />
        </label>
      </div>
      <div>
        <label>
          Lines
          <input
            type="checkbox"
            checked={layerVisibility.lines}
            onChange={() => handleLayerToggle('lines')}
          />
        </label>
      </div>
      <div>
        <label>
          Polygons
          <input
            type="checkbox"
            checked={layerVisibility.polygons}
            onChange={() => handleLayerToggle('polygons')}
          />
        </label>
      </div>
      <div ref={mapRef} style={{ width: '100%', height: '500px' }}></div>
      <div ref={popupRef} className="ol-popup">{popupContent}</div>
    </div>
  );
};

export default MapComponent;
